import defined from "../../../Core/defined.js";
import DeveloperError from "../../../Core/DeveloperError.js";
import Cartesian3 from "../../../Core/Cartesian3.js";
import {emMod} from "./EmWrapperManager.js";
import Matrix4 from "../../../Core/Matrix4.js";
import defaultValue from "../../../Core/defaultValue.js";
import Matrix3 from "../../../Core/Matrix3.js";
import Transforms from "../../../Core/Transforms.js";

const ptTransScratch = new Cartesian3();
const ptScaleScratch = new Cartesian3();
const ptMat3Scratch = new Matrix3();
const ptRotateScratch = new Matrix3();
const ptMat4Scratch = new Matrix4();

class EmLBDeal {
    /**
     * @constructor
     */
    constructor() {
    }

    /**
     * init EmLBDeal
     * @param {string} proj4Str 投影坐标系描述字符串
     * @param {Cartesian3} centerMapPt 中心点经纬度坐标
     * @returns {boolean}
     */
     init(proj4Str, centerMapPt) {
        if (!defined(emMod)) {
            throw new DeveloperError("initWebAssembly初始化尚未完成");
        }
        this._LBDeal = new emMod.LBDeal();
        if (!this._LBDeal.Init(proj4Str, centerMapPt.x, centerMapPt.y, centerMapPt.z)) {
            emMod.destroy(this._LBDeal);
            this._LBDeal = undefined;
            return false;
        }

        this._centerENMatrix = Transforms.eastNorthUpToFixedFrame(Cartesian3.fromDegrees(centerMapPt.x, centerMapPt.y, centerMapPt.z));

        this._matrixArray = new Float64Array(Matrix4.toArray(Matrix4.IDENTITY));
        this._pMatrixAry = emMod._malloc(this._matrixArray.byteLength);

        this._oneTyped32Array = new Float32Array([0, 0, 0]);
        this._pOnePt32Ary = emMod._malloc(this._oneTyped32Array.byteLength);

        this._oneTyped64Array = new Float64Array([0, 0, 0]);
        this._pOnePt64Ary = emMod._malloc(this._oneTyped64Array.byteLength);
        return true;
    }

    /**
     * destory EmLBDeal
     */
    destroy() {
        if (defined(this._LBDeal)) {
            emMod.destroy(this._LBDeal);
            this._LBDeal = undefined;

            emMod._free(this._pMatrixAry);
            emMod._free(this._pOnePt32Ary);
            emMod._free(this._pOnePt64Ary);
        }
    }

    /**
     * rel center proj pt ary to rel map center cartesian pt ary
     * @param {Number[]} points
     * @param {boolean} bYup true is Y up  false is Z up
     * @param {Matrix4} [matrix]
     */
     computeProjToCartesianAry(points, bYup, matrix) {
        this.computeProjToCartesianAryImp(matrix, points, bYup, false, this._LBDeal, false);
    }

    /**
     * cartesian pt ary to rel center proj pt ary
     * @param {Number[]} points
     * @param {boolean} bYup true is Y up  false is Z up
     * @param {Matrix4} [matrix]
     */
    computeCartesianToProjAry(points, bYup, matrix) {
        this.computeProjToCartesianAryImp(matrix, points, bYup, true, this._LBDeal, false);
    }

    /**
     * rel center proj pt ary to rel map center cartesian pt
     * @param {Cartesian3} cartesian
     * @param {Matrix4} [matrix]
     * @param {Cartesian3} [result]
     * @returns {Cartesian3}
     */
    computeProjToCartesian(cartesian, matrix, result) {
        let points = [cartesian.x, cartesian.y, cartesian.z];
        this.computeProjToCartesianAryImp(matrix, points, false, false, this._LBDeal, true);
        if (!defined(result)) {
            return new Cartesian3(points[0], points[1], points[2]);
        }
        result.x = points[0];
        result.y = points[1];
        result.z = points[2];
        return result;
    }

    /**
     * cartesian pt to rel center proj pt
     * @param {Cartesian3} cartesian
     * @param {Matrix4} [matrix]
     * @param {Cartesian3} [result]
     * @returns {Cartesian3}
     */
    computeCartesianToProj(cartesian, matrix, result) {
        let points = [cartesian.x, cartesian.y, cartesian.z];
        this.computeProjToCartesianAryImp(matrix, points, false, true, this._LBDeal, true);
        if (!defined(result)) {
            return new Cartesian3(points[0], points[1], points[2]);
        }
        result.x = points[0];
        result.y = points[1];
        result.z = points[2];
        return result;
    }

    /**
     * map degree ary to proj
     * @param {Number[]} points
     * @param {boolean} bSubstratProjCenter
     */
    computeDegreeToProjAry(points, bSubstratProjCenter) {
        this.computeDegreeToProjImp(points, bSubstratProjCenter, false, this._LBDeal, false);
    }

    /**
     * proj pt ary to map degree
     * @param {Number[]} points
     * @param {boolean} bInputRelProjCenter
     */
    computeProjToDegreeAry(points, bInputRelProjCenter) {
        this.computeDegreeToProjImp(points, bInputRelProjCenter, true, this._LBDeal, false);
    }

    /**
     * map degree to proj
     * @param {Cartesian3} cartesian
     * @param {boolean} bSubstratProjCenter
     * @param {Cartesian3} [result]
     * @returns {Cartesian3}
     */
     computeDegreeToProj(cartesian, bSubstratProjCenter, result) {
        let points = [cartesian.x, cartesian.y, cartesian.z];
        this.computeDegreeToProjImp(points, bSubstratProjCenter, false, this._LBDeal, true);
        if (!defined(result)) {
            return new Cartesian3(points[0], points[1], points[2]);
        }
        result.x = points[0];
        result.y = points[1];
        result.z = points[2];
        return result;
    }

    /**
     * proj pt to map degree
     * @param {Cartesian3} cartesian
     * @param {boolean} bInputRelProjCenter
     * @param {Cartesian3} [result]
     * @returns {Cartesian3}
     */
    computeProjToDegree(cartesian, bInputRelProjCenter, result) {
        let points = [cartesian.x, cartesian.y, cartesian.z];
        this.computeDegreeToProjImp(points, bInputRelProjCenter, true, this._LBDeal, true);
        if (!defined(result)) {
            return new Cartesian3(points[0], points[1], points[2]);
        }
        result.x = points[0];
        result.y = points[1];
        result.z = points[2];
        return result;
    }

    /**
     * cvt rel proj matrix to cartesian matrix
     * @param {Matrix4} matrix
     * @param {Matrix4} [result]
     * @returns {Matrix4}
     */
    cvtRelProjMatrixToCartesianMatrix(matrix, result) {
        if (!defined(result)) {
            result = new Matrix4();
        }

        Matrix4.getTranslation(matrix, ptTransScratch);
        Matrix4.getScale(matrix, ptScaleScratch);
        Matrix4.getMatrix3(matrix, ptMat3Scratch);
        Matrix3.getRotation(ptMat3Scratch, ptRotateScratch);

        Matrix4.fromScale(ptScaleScratch, result);
        Matrix4.multiplyByMatrix3(result, ptRotateScratch, result);

        this.computeProjToCartesian(ptTransScratch, undefined, ptTransScratch);
        Matrix4.multiplyByPoint(this._centerENMatrix, ptTransScratch, ptTransScratch);
        Transforms.eastNorthUpToFixedFrame(ptTransScratch, undefined, ptMat4Scratch);

        Matrix4.multiply(ptMat4Scratch, result, result);
        return result;
    }

    computeProjToCartesianAryImp(matrix, points, bYup, reverse, cDeal, isOne) {
        if (!defined(cDeal)) {
            throw new DeveloperError("请先执行 init进行初始化");
        }

        let typedArray;
        let pPtAry;
        if (!isOne) {
            typedArray = new Float32Array(points);
            pPtAry = emMod._malloc(typedArray.byteLength);
        } else {
            typedArray = this._oneTyped32Array;
            typedArray.set(points);
            pPtAry = this._pOnePt32Ary;
        }
        emMod.HEAPF32.set(typedArray, pPtAry / 4);

        matrix = defaultValue(matrix, Matrix4.IDENTITY);
        this._matrixArray.set(Matrix4.toArray(matrix));
        emMod.HEAPF64.set(this._matrixArray, this._pMatrixAry / 8);

        if (reverse) {
            cDeal.ComputeCartesianToProj(
                pPtAry,
                typedArray.length,
                this._pMatrixAry,
                bYup
            );
        } else {
            cDeal.ComputeProjToCartesian(
                pPtAry,
                typedArray.length,
                this._pMatrixAry,
                bYup
            );
        }

        for (let index = 0; index < points.length; ++index) {
            points[index] = emMod.HEAPF32[(pPtAry >> 2) + index];
        }

        if (!isOne) {
            emMod._free(pPtAry);
        }
    }

    computeDegreeToProjImp(points, bRel, reverse, cDeal, isOne) {
        if (!defined(cDeal)) {
            throw new DeveloperError("请先执行 init进行初始化");
        }

        let typedArray;
        let pPtAry;
        if (!isOne) {
            typedArray = new Float64Array(points);
            pPtAry = emMod._malloc(typedArray.byteLength);
        } else {
            typedArray = this._oneTyped64Array;
            typedArray.set(points);
            pPtAry = this._pOnePt64Ary;
        }
        emMod.HEAPF64.set(typedArray, pPtAry / 8);

        if (reverse) {
            cDeal.TranformProjToDegree(pPtAry, typedArray.length, bRel);
        } else {
            cDeal.TranformDegreeToProj(pPtAry, typedArray.length, bRel);
        }

        for (let index = 0; index < points.length; ++index) {
            points[index] = emMod.HEAPF64[(pPtAry >> 3) + index];
        }

        if (!isOne) {
            emMod._free(pPtAry);
        }
    }
}

export default EmLBDeal;
